<template>
  <div>
    <p>Generate Java Enum Code From Chinese</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <input v-model="class_name"><input v-model="handle_case" type="button" @click="upper_case()">
    <br>
    <textarea v-model="source" id="source" rows="50" cols="80" placeholder="source text"></textarea>
    <textarea v-model="source_en" id="source_en" rows="50" cols="80" placeholder="source en text"></textarea>
    <textarea v-model="output" id="output" rows="50" cols="80" placeholder="output" readonly></textarea>
    <br>
    <div>
      <input value="format" type="button" @click="format()">
      <input value="format_en" type="button" @click="format_en()">
      <input value="handle" type="button" @click="handle()">
      <input value="handle_all" type="button" @click="handle_all()">
    </div>
  </div>
</template>

<script>

export default {
  name: 'GenerateJavaEnumCodeFromChinese',
  data () {
    return {
      class_name: 'ClassTable',
      old_class_name: '',
      handle_case: 'upper case',
      code_type: 'Integer',
      source: '0.否\n1.是',
      source_en: 'NO\nYES',
      output: '',
      enlist: null,
      enum_class_template: 'public enum %c {\n\n%s\n\n    ;\n\n    private %t code;\n\n    private String description;\n\n    private %c(%t code, String description) {\n        this.code = code;\n        this.description = description;\n    }\n\n    public %t getCode() {\n        return code;\n    }\n\n    public String getDescription() {\n        return description;\n    }\n}',
      field_default_template: '    %e(%n, "%z")'
    }
  },
  methods: {
    format_en () {
      let temp = ''
      this.enlist = []
      const sourceArr = this.source_en.split('\n').map(e => e.trim()).map(e => e.substring(e.indexOf('.') + 1, e.length).trim()).filter(e => e)
      for (let i = 0; i < sourceArr.length; i++) {
        const ii = sourceArr[i].toUpperCase().replaceAll(' ', '_')
        this.enlist.push(ii)
        if (i === sourceArr.length - 1) {
          temp = temp + ii
        } else {
          temp = temp + ii + '\n'
        }
        this.source_en = temp
      }
    },
    format () {
      let temp = ''
      const sourceArr = this.source.split(' ')
      for (let i = 0; i < sourceArr.length; i++) {
        const ii = sourceArr[i]
        if (i === sourceArr.length - 1) {
          temp = temp + ii
        } else {
          temp = temp + ii + '\n'
        }
      }
      this.source = temp
    },
    handle () {
      this.format()
      this.output = ''
      let fieldTemp = ''
      const sourceArr = this.source.split('\n')
      if (sourceArr.length < 0) {
        alert('empty')
      }
      if (isNaN(Number(sourceArr[0].split('.')[0]))) {
        this.code_type = 'String'
        this.field_default_template = '    %e("%n", "%z")'
      }
      for (let i = 0; i < sourceArr.length; i++) {
        const ii = sourceArr[i].split('.')
        if (i === sourceArr.length - 1) {
          fieldTemp = fieldTemp + this.field_default_template.replaceAll('%e', this.enlist[i]).replaceAll('%n', ii[0]).replaceAll('%z', ii[1])
        } else {
          fieldTemp = fieldTemp + this.field_default_template.replaceAll('%e', this.enlist[i]).replaceAll('%n', ii[0]).replaceAll('%z', ii[1]) + ',\n\n'
        }
      }
      this.output = this.output + this.enum_class_template.replaceAll('%s', fieldTemp).replaceAll('%c', this.class_name).replaceAll('%t', this.code_type)
      navigator.clipboard.writeText(this.output).then(() => console.log('copied'))
    },
    upper_case () {
      if (this.handle_case === 'recover') {
        this.class_name = this.old_class_name
        this.handle_case = 'upper case'
        return
      }
      const tmp = this.class_name
      let result = ''
      for (let i = 0; i < tmp.length; i++) {
        const e = tmp[i]
        if (i === 0) {
          result += e.toUpperCase()
        } else {
          if (e.charCodeAt() < 97) {
            result += '_' + e
          } else {
            result += e.toUpperCase()
          }
        }
      }
      this.old_class_name = this.class_name
      this.handle_case = 'recover'
      this.class_name = result
    },
    handle_all () {
      this.format()
      this.format_en()
      this.handle()
    }
  }
}
</script>
